.lhq-drag-container {
  display: flex;
  width: 100%;
  height: 100%;
  overflow: hidden;
}

.lhq-skeleton {
  border: 2px solid #d9d9d9;
  cursor: e-resize;
}

.lhq-skeleton:hover {
  border-color: #2e75d3;
}

.lhq-fold-rowContainer {
  width: 100%;
  position: relative;
  border: 1px solid #d9d9d9;
  border-radius: 6px;
  box-shadow: 0 2px 0 rgba(0, 0, 0, 0.02);
}
.lhq-fold-columnContainer {
  height: 100%;
  position: relative;
  border: 1px solid #d9d9d9;
  border-radius: 6px;
  box-shadow: 0 2px 0 rgba(0, 0, 0, 0.02);
}
.lhq-fold-selectContainer {
  border-color: #2e75d3;
}

.lhq-fold-arrowRowContainer {
  position: absolute;
  bottom: 0;
  right: 50%;
  width: 25px;
  height: 25px;
  z-index: 2;
}
.lhq-fold-arrowColumnContainer {
  position: absolute;
  right: 0;
  top: 50%;
  width: 25px;
  height: 25px;
  z-index: 2;
}

.lhq-arrow {
  width: 100%;
  height: 100%;
  padding: 4px;
  line-height: 100%;
}
.lhq-row-arrow-open {
  border-radius: 8px 8px 0 0;
  border-bottom: 0;
}
.lhq-row-arrow-close {
  border-radius: 0 0 8px 8px;
  border-top: 0;
}
.lhq-column-arrow-open {
  border-radius: 8px 0 0 8px;
  border-right: 0;
}
.lhq-column-arrow-close {
  border-radius: 0 8px 8px 0;
  border-left: 0;
}

.lhq-up-arrow {
  width: 0;
  height: 0;
  transform: translate(0, 50%);
  border-top: 20px black;
  border-left: 20px transparent;
  border-right: 20px transparent;
  border-bottom: 20px transparent;
}
.lhq-down-arrow {
  width: 0;
  height: 0;
  transform: translate(0, -50%);
  border-top: 20px transparent;
  border-left: 20px transparent;
  border-right: 20px transparent;
  border-bottom: 20px black;
}
.lhq-left-arrow {
  width: 0;
  height: 0;
  transform: translate(50%, 0);
  border-top: 20px transparent;
  border-left: 20px black;
  border-right: 20px transparent;
  border-bottom: 20px transparent;
}
.lhq-right-arrow {
  width: 0;
  height: 0;
  transform: translate(-50%, 0);
  border-top: 20px transparent;
  border-left: 20px transparent;
  border-right: 20px black;
  border-bottom: 20px transparent;
}

.container {
  width: 65vw;
  margin: 5em auto;
}
.container .title {
  text-align: center;
  padding: 1em 0em;
}
.main {
  display: flex;
  flex-direction: column;
  width: 50vw;
  margin: 0 auto;
}
.drag,
.fold {
  display: flex;
  flex-direction: column;
  margin: 0.5em 0;
}

.drag-title,
.fold-title {
  font-size: 24px;
  margin: 0.5em;
}

